'use client';

import './styles/globals.css'; // Verifique se o caminho para o seu arquivo CSS está correto.
import Navbar from './navbar/navbar'; // Ajuste conforme o caminho do seu componente
import Footer from './footer/footer'; // Ajuste conforme o caminho do seu componente Footer
import { usePathname } from 'next/navigation';

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname();

  return (
    <html lang="pt-br">
      <head>
        {/* Outros elementos dentro de <head> */}
      </head>
      <body className="flex flex-col min-h-screen">
        {/* Se a URL for a página de login, não exibe o Navbar */}
        {pathname !== '/' && <Navbar />}
        
        {/* Conteúdo da página */}
        <main className="flex-grow">{children}</main>
        
        {/* Footer exibido em todas as páginas, exceto na página de login */}
        {pathname !== '/' && <Footer />}
      </body>
    </html>
  );
}
